<template>
  <div class="page-booking">

    <mt-header title="选择房间">
    <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>

    <div class="box">
      <div class="house-title">
        <i class="common-icon icon-house"></i>
        <span class="house-title-text">选择房间</span>

        <span v-if="roomName" class="house-title-choose">已选：{{roomName}}</span>
      </div>

      <div class="house-list">
        <mt-button
            v-for="(item, index) in rooms"
            key="index"
            :class="{'primary':item._id == roomId}"
            size="small"
            @click.native="handleHouse(item)">
              {{item.room_name}}
            </mt-button>
      </div>
    </div>

    <div class="box" v-if="beds.max_num>0">
      <div class="house-title">
        <i class="common-icon icon-bed"></i>
        <span class="house-title-text">选择床位</span>

        <span class="house-title-hasbed"><i class="circle-gray"></i>床位可选</span>
      </div>

      <div class="bed-list">
        <table>
          <thead>
            <tr>
              <td>下铺</td>
              <td>中铺</td>
              <td>上铺</td>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>
                <div class="bed-item" v-for="(item, index) in beds.bot" key="index">
                  <span v-if="item.berth_status == 1">
                    <label :for='"wh-"+item.berth_id'>
                      <input :id='"wh-"+item.berth_id' class="mint-radio-input" type="checkbox" v-model='bedList' :value="item.berth_id" />
                      <span class="mint-radio-core"></span>
                    </label>
                  </span>
                  
                  <span v-else-if="item.berth_status == 2" class="orange">已预订</span>
                  <span v-else-if="item.berth_status == 3" class="gray">无</span>
                </div>
              </td>

              <td>
                <div class="bed-item" v-for="(item, index) in beds.mid" key="index">
                  <span v-if="item.berth_status == 1">
                    <label :for='"wh-"+item.berth_id'>
                      <input :id='"wh-"+item.berth_id' class="mint-radio-input" type="checkbox" v-model='bedList' :value="item.berth_id" />
                      <span class="mint-radio-core"></span>
                    </label>
                  </span>
                  
                  <span v-else-if="item.berth_status == 2" class="orange">已预订</span>
                  <span v-else-if="item.berth_status == 3" class="gray">无</span>
                </div>
              </td>

              <td>
                <div class="bed-item" v-for="(item, index) in beds.top" key="index">
                  <span v-if="item.berth_status == 1">
                    <label :for='"wh-"+item.berth_id'>
                      <input :id='"wh-"+item.berth_id' class="mint-radio-input" type="checkbox" v-model='bedList' :value="item.berth_id" />
                      <span class="mint-radio-core"></span>
                    </label>
                  </span>
                  
                  <span v-else-if="item.berth_status == 2" class="orange">已预订</span>
                  <span v-else-if="item.berth_status == 3" class="gray">无</span>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="fix-bottom fix-order">
      <div class="order-summary">
        <span class="order-price">合计：¥{{house_sum}}</span>
        <span class="order-extra">共{{house_days}}天  {{house_name}}  {{bedList.length}}个床位</span>
      </div>
      <mt-button @click.native="handleNext" size="small" type="primary">下一步</mt-button>
    </div>

  </div>
</template>

<script>
import '../../css/booking.css';
import ajaxUrl from '../../common/ajaxUrl'
import Util from '../../common/Util'

export default {
  name: 'page-house',

  components: {

  },

  data() {
    return {
      house_name:'',
      house_price: 0,
      house_days: 0,
      house_sum: 0,
      roomId: '',
      roomName: '',
      rooms: [],
      beds: {},
      bedList: [],
      roomListApi: ajaxUrl.build_room_list,
      roomInfoApi: ajaxUrl.build_room_info
    };
  },

  created() {
    this.$indicator.open();

    let data = localStorage.getItem("booking-house");
    if(data && data != ""){
      let booking = JSON.parse(data);

      if(Util.isObject(booking)){
        this.house_name = booking.name;
        this.house_price = booking.price;
        this.house_days = booking.days;
      }
    }

    setTimeout(() => {
        this.getRooms();
    }, 500);
    
  },

  watch: {
    roomId: function (val, oldVal) {
      if(val != ''){
        this.$indicator.open();

        this.bedList = [];
        this.house_sum = 0;
        setTimeout(() => {
          this.getBeds();
        }, 500);
      }
    },
    bedList: function (val, oldVal) {
      if(val.length > 0){
        this.house_sum = this.house_price * this.house_days * val.length;
      }else{
        this.house_sum = 0;
      }
    }
  },

  methods: {
    back () {
      window.history.back();
    },

    getRooms () {
      let query = this.$route.query;
      if(!query.name && !query.tel && !query.identity){
        this.$toast("参数不合法");
      }

      this.$http.post(this.roomListApi, {
        'predetermine_name': decodeURIComponent(query.name),
        'predetermine_phone': query.tel,
        'predetermine_id_card': query.identity,
        'predetermine_type': query.type,
        'token': Util.getToken()
      }).then((res) => {
        let data = res.data;
        if(data.status.code == 0){
          let floor = data.result.floor;
          if(floor && Util.isObject(floor)){
            this.rooms = floor['1'].rooms;
          }
        }else{
          this.$toast(data.status.msg);
        }
      }, (res) => {
        this.$toast('加载内容失败');
      }).then(() => {
        this.$indicator.close();
      });
    },

    getBeds () {
      this.$http.post(this.roomInfoApi, {
        'room_id': this.roomId,
        'token': Util.getToken()
      }).then((res) => {
          let data = res.data;
          
          if(data.status.code == 0){
            this.beds = data.result.berths;
          }else if(data.status.code == 504){
            this.$toast(data.status.msg);
            setTimeout(() => {
              this.goLogin();
            }, 2000);
          }
      }, (res) => {
          this.$toast('加载内容失败');
      }).then(() => {
          this.$indicator.close();
      });
    },

    handleHouse (item) {
      this.roomId = item._id;
      this.roomName = item.room_name;
    },

    handleNext () {
      let isLogin = Util.isLogin();
      if(!isLogin){
        this.goLogin();
        return false;
      }

      if(this.bedList.length < 1){
        this.$toast('请先选择床位');
        return false;
      }

      console.log('选中房间:' + this.roomName + '选中床位:' + this.bedList.join(','));

      this.$router.push({ path: '/order/place', query: {bids: this.bedList.join(',')}})
    },

    goLogin (){
      this.$router.push({ path: '/user/login', query: { refer: 'house'}})
    }
  }
};
</script>




