<template>

  <div class="mix-box" v-show="title != ''">
    <img class="mix-pic" v-lazy="image" />
    <div class="mix-info">
      <h3 class="mix-title">{{ title }} <span class="orange">{{ salary }}</span></h3>
      <p class="mix-desc">{{ source }}</p>
    </div> 
  </div>

</template>

<script>

  export default {
    name: 'mix-box',

    props: {
      image: {
        type: String,
        default: ''
      },
      title: {
        type: String,
        default: ''
      },
      salary: {
        type: String,
        default: ''
      },
      source: {
        type: String,
        default: ''
      }
    }

  };
</script>

<style lang="css">

</style>



