<template>
  <div class="page-process">
    <mt-header class="home-header" title="微海生涯">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
      <router-link to="/order/list" slot="right">
          <mt-button>订单</mt-button>
        </router-link>
    </mt-header>

    <div class="user-info">
      <span class="mask">
        <img class="avatar" :src="avator" />
        <input class="upload" type="file" @change="uploadAvatar($event)" name="file" accept="image/*" />
      </span>
      <p class="name">
        <input type="text" @blur="changeName" v-model="nick_name" />
      </p>
      <p class="tel"> {{phone}} </p>
    </div>

    <dl class="timeline">
      <dt>2017年</dt>
      <dd>
        <div class="item-time"> <span>06</span>5月</div>
        <div class="item-line"></div>
        <div class="item-icon"></div>
        <div class="item-content">
          培训圆满结束，获得高度评价：认真、努力、勤奋、好学
        </div>
      </dd>
      <dd>
        <div class="item-time"> <span>21</span>3月</div>
        <div class="item-line"></div>
        <div class="item-icon"></div>
        <div class="item-content">
          在杭州武林银泰百货培训
        </div>
      </dd>
      <dd>
        <div class="item-time"> <span>03</span>2月</div>
        <div class="item-line"></div>
        <div class="item-icon"></div>
        <div class="item-content">
          升职：成为海底捞(杭州区)大堂经理
        </div>
      </dd>
    </dl>
    <dl class="timeline">
      <dt>2016年</dt>
      <dd>
        <div class="item-time"> <span>09</span>12月</div>
        <div class="item-line"></div>
        <div class="item-icon"></div>
        <div class="item-content">
          在上海，加入微海大家庭
        </div>
      </dd>
    </dl>

  </div>
</template>

<script>
import commonTitle from '../../common/title.vue'
import ajaxUrl from '../../common/ajaxUrl'
import Util from '../../common/Util'

export default {
  name: 'page-home',

  components: {
    commonTitle
  },

  data : function(){
    return{
      read: true,

      nick_name: '',
      phone: '',
      avator: '',
      updateApi: ajaxUrl.update_user_info,
    }
  },

  created() {

    let data = localStorage.getItem("user-info");
    if(data && data != ""){
      let userInfo = JSON.parse(data);

      this.phone = userInfo.phone;
      if(userInfo.avator != ''){
        this.avator = userInfo.avator;
      }
      if(userInfo.nick_name != ''){
        this.nick_name = userInfo.nick_name;
      }

    }else{
      this.$router.push({ path: 'login', query: { refer: 'home'}})
      return false;
    }
  },

  methods: {
    back () {
      window.history.back();
    },

    changeName (e) {
      this.changeUserInfo();
    },

    changeName (e){
      this.$http.post(this.updateApi, {
        'nick_name': this.nick_name,
        'avatar': this.avatar,
        'token': Util.getToken()
      }).then((res) => {
          let data = res.data;
          if(data){
            if(data.status.code == 0){
              this.userInfo = data.result.user_info;
              //存储用户信息
              let dataString = JSON.stringify(this.userInfo); 
              localStorage.setItem("user-info", dataString);

            }else if(data.status.code == 505){
              this.$toast('上传失败,请修改图片尺寸');
            }
          }
      }, (res) => {
          this.$toast('更新信息失败');
      });
    },

    uploadAvatar (e){

      let callback = (data, i) => {
          
          if(data.status.code == 0){
            this.userInfo = data.result.user_info;
            //存储用户信息
            let dataString = JSON.stringify(this.userInfo); 
            localStorage.setItem("user-info", dataString);
            this.avator = this.userInfo.avator;

          }else if(data.status.code == 505){
            this.$toast('头像修改失败');
          }
      }

      this.uploadPic(e.currentTarget.files, callback);
    },

    //上传图片
    uploadPic: function(files, callback){
        var self = this,
            _url = this.updateApi,
            i = 0;

        function _submit (file){
            var fd = new FormData();
            var xhr = new XMLHttpRequest();

            fd.append('avator', file);
            fd.append('nick_name', self.nick_name);
            fd.append('token', Util.getToken());

            xhr.addEventListener("load", function(e){
                var result = JSON.parse(e.target.responseText);
                
                if(callback){
                    callback(result,i);
                }

            }, false);

            xhr.open("POST", _url, true);
            xhr.send(fd);
        }

        _submit(files[i]);
    }
  }
};
</script>

<style lang="css">
  .home-header{
    position: absolute;
    left:0;
    top:0;
    width:100%;
    background: none;
  }
  .home-header .mint-button-text{
    font-size:14px;
  }

  .user-info{
    display:flex;
    flex-direction: column;
    height:200px;
    margin:0;
    color:#fff;
    text-align:center;
    background:url(../../../images/mine_bg@3x.jpg) #fff 0 0 no-repeat;
    background-size: 100% 100%;
  }
  .user-info .avatar{
    width:60px;
    height:60px;
    border:2px solid #fff;
    border-radius:32px;
  }

  .mask{
    position:relative;
    width:64px;
    height:64px;
    margin:60px auto 12px;
  }
  .mask .upload{
    position:absolute;
    width:64px;
    height:64px;
    left:0;
    top:0;
    opacity:0;
  }
  
  .user-info p{
    line-height:24px;
  }
  .user-info p input{
    text-align: center;
    background: none;
    border: none;
    color: #fff;
    font-size:14px;
  }
  .user-info .name input{
    font-size:18px;
  }

  .timeline{
    background:#fff;
    border-bottom:1px solid #E9E9E9;
    overflow:hidden;
  }
  .timeline dt{
    height:36px;
    line-height:40px;
    padding-left:15px;
    font-size: 15px;
    color: #828282;
    background:#f0f0f0;
    border-bottom:1px solid #E9E9E9;
  }
  .timeline dd{
    position: relative;
    min-height: 50px;
    padding-top: 35px;
  }
  .item-time{
    position: absolute;
    left: 22px;
    top: 25px;
    width: 50px;
    font-size: 14px;
    line-height: 20px;
  }
  .item-time span{
    display: block;
    font-size: 20px;
    color: #1E1E1E;
  }
  .item-line{
    left: 70px;
    position: absolute;
    height: 100%;
    border: 1px solid #E9E9E9;
  }

  .item-icon{
    position: absolute;
    left:66px;
    box-sizing: border-box;
    border-radius: 50%;
    background: #0977C5;
    width: 10px;
    height: 10px;
  }

  .item-content{
    position: relative;
    padding-bottom: 10px;
    left: 90px;
    top: -3px;
    width:70%
  }
</style>


