<template>
  <div class="page-renew">

    <mt-header title="我要续订">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="box check-info">
      <div class="check-date" @click.stop="handleDate">
        <div class="check-in">入住日期 <span>{{begin}}</span></div>
        <div class="check-day">共{{count}}天</div>
        <div class="check-out">退房日期 <span>{{end}}</span></div>
      </div>
    </div>

    <div class="fix-bottom">
      <mt-button @click.native="handleRenew" size="large" type="primary">提交订单</mt-button>
    </div>
    
  </div>
</template>

<script>
import ajaxUrl from '../../common/ajaxUrl'
import Util from '../../common/Util'

export default {
  name: 'page-renew',

  components: {

  },

  data() {
    return {
      begin: '',
      end:'',
      count: 1,
      start_time: '',
      end_time: '',
      oid: '',
      renewApi: ajaxUrl.order_renew
    };
  },

  created() {
    let begin = Util.getToday();
    let end = Util.getTomorrow();

    let data = localStorage.getItem("booking-date");
    if(data && data != ""){
      let date = JSON.parse(data);

      if(Util.isObject(date)){
        begin = date.begin;
        end = date.end;
        this.count = date.count;
      }
    }

    //界面显示
    this.begin = begin[1]+"月"+begin[2]+"日",
    this.end = end[1]+"月"+end[2]+"日";

    //后端参数
    this.start_time = begin.join("-");
    this.end_time = end.join("-");

    //获取ID
    this.oid = this.$route.query.id;

  },

  destroyed () {
    localStorage.setItem("booking-date", ""); //清除日期存储
  },

  methods: {
    handleDate () {
      this.$router.push({ path: '/dormitory/date', query: { refer: 'renew' }})
    },

    handleRenew () {
      this.$http.post(this.renewApi, {
        'order_id': this.oid, 
        'predetermine_start_time': this.start_time,  
        'predetermine_end_time': this.end_time,  
        'token': Util.getToken()
      }).then((res) => {
          let data = res.data;
          if(data.status.code == 0){
            let result = data.result;

            if(result && result.order.order_id){
              this.$router.push({ path: 'detail', query: { id: result.order.order_id}})
            }
          }else if(data.status.code == 504){
            this.$toast(data.status.msg);
            setTimeout(() => {
              this.goLogin();
            }, 2000);
          }else if(data.status.code == 500){
            this.orderConfirm();
          }
      }, (res) => {
          this.$toast('预定失败了');
      });
      
    },

    orderConfirm () {
      let msg = '您的房间<span>'+this.begin+'<\/span>至<span>'+this.end+'<\/span>已经被人预定走喽，请修改时间'

      this.$messagebox({
        title: '档期已满',
        message: msg,
        type:'confirm',
        showCancelButton: true,
        confirmButtonText: '不续订了',
        cancelButtonText: '修改时间',
      }).then(action => {
        if(action == "confirm"){
          window.history.back();
        }
      });
    },

    goLogin () {
      this.$router.push({ path: '/user/login', query: { refer: 'order-renew'}})
    }

  }
};
</script>



