<template>
  <div class="page-order-detail">

    <mt-header :title="order_status">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>

    <div class="box">
      <common-title iconClass="icon-house" text="宿舍信息"> </common-title>
      <div class="box-desc">
        <ul class="list-info">
          <li>
            <label>住 宿 费：</label>
            <span v-if="build.total_amount" class="orange"> ￥{{build.total_amount}} </span>
          </li>
          <li>
            <label>性     别：</label>
            <span v-if="build.sex == 1"> 男 </span>
            <span v-else-if="build.sex == 2"> 女 </span>
          </li>
          <li>
            <label>房间信息：</label>
            <span> {{build.room_name}} </span>
          </li>
          <li>
            <label>床位信息：</label>
            <span> {{build.berth_name}}</span>
          </li>
          <li>
            <label>入住时间：</label>
            <span> {{build.date_range}} </span>
          </li>
          <li>
            <label>宿舍地点：</label>
            <span> {{build.building_address}} </span>
          </li> 
        </ul>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-user" text="个人信息"> </common-title>
      <div class="box-desc">
        <ul class="list-info">
          <li>
            <label>入 住 人：</label>
            <span> {{user.predetermine_name}} </span>
          </li>
          <li>
            <label>手机号码：</label>
            <span> {{user.predetermine_phone}} </span>
          </li>
          <li>
            <label>身份证号：</label>
            <span> {{user.predetermine_id_card}} </span>
          </li>
          <li>
            <label>入住类型：</label>
            <span v-if="user.predetermine_type == 1"> 培训入住 </span>
            <span v-else-if="user.predetermine_type == 2"> 工作入住 </span>
            <span v-else> 其他 </span>
          </li>
        </ul>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-info" text="订单信息"> </common-title>
      <div class="box-desc">
        <ul class="list-info">
          <li>
            <label>订单编号：</label>
            <span> {{order.order_sn}} </span>
          </li>
          <li>
            <label>订单时间：</label>
            <span> {{order.create_at}} </span>
          </li>
        </ul>
      </div>
    </div>

    <div v-if="order.order_status != 3" class="fix-bottom">
      <mt-button v-if="order.order_status == 1" size="large" type="primary" @click.native="handlePay">
        确认支付
      </mt-button>
      <mt-button v-else-if="order.order_status == 2" size="large" type="primary" @click.native="handleRenew">
        我要续订
      </mt-button>
    </div>
    
  </div>
</template>

<script>
import commonTitle from '../../common/title.vue'
import ajaxUrl from '../../common/ajaxUrl'
import Util from '../../common/Util'

export default {
  name: 'page-order-detail',

  components: {
      commonTitle
  },

  data() {
    return {
      order_status:'',
      build:{},
      user:{},
      order:{},
      oid: '',
      orderInfoApi: ajaxUrl.order_info
    };
  },

  created() {
    this.$indicator.open();

    //获取ID
    this.oid = this.$route.query.id;
    setTimeout(() => {
        this.oid != '' && this.getData();

        this.$indicator.close();
    }, 500);

  },

  methods: {
    back () {
      window.history.back();
    },
    
    getData () {
      this.$http.post(this.orderInfoApi, {
        'order_id': this.oid,
        'token': Util.getToken()
      }).then((res) => {
        let data = res.data;
        if(data.status.code == 0){
          let result = data.result;

          if(result){
            this.build = result.building;
            this.user = result.predeterminer;
            this.order = result.order;
            if(this.order.create_at){
              this.order.create_at = Util.formatDate(parseInt(this.order.create_at));
            }
            if(this.order.order_status == 1){
              this.order_status = '待支付';
            }else if(this.order.order_status == 2){
              this.order_status = '已支付';
            }else{
              this.order_status = '已取消';
            }
          }
        }else if(data.status.code == 504){
          this.$toast(data.status.msg);
          setTimeout(() => {
            this.goLogin();
          }, 2000);
        }
      }, (res) => {
        this.$toast('加载内容失败');
      });
    },

    goLogin (){
      this.$router.push({ path: '/user/login', query: { refer: 'order-detail'}})
    },

    handleRenew () {
      this.$router.push({ path: 'renew', query: { id: this.oid }})
    },

    handlePay () {
      //todo
      console.log('去支付')

      if (typeof WeixinJSBridge == "undefined"){
         if( document.addEventListener ){
             document.addEventListener('WeixinJSBridgeReady', this.onBridgeReady, false);
         }else if (document.attachEvent){
             document.attachEvent('WeixinJSBridgeReady', this.onBridgeReady); 
             document.attachEvent('onWeixinJSBridgeReady', this.onBridgeReady);
         }
      }else{
         this.onBridgeReady();
      }
      
    },

    onBridgeReady (){

       WeixinJSBridge.invoke(
           'getBrandWCPayRequest', {
               "appId":"wx2421b1c4370ec43b",     //公众号名称，由商户传入     
               "timeStamp":"1395712654",         //时间戳，自1970年以来的秒数     
               "nonceStr":"e61463f8efa94090b1f366cccfbbb444", //随机串     
               "package":"prepay_id=u802345jgfjsdfgsdg888",     
               "signType":"MD5",         //微信签名方式：     
               "paySign":"70EA570631E4BB79628FBCA90534C63FF7FADD89" //微信签名 
           },
           function(res){     
               if(res.err_msg == "get_brand_wcpay_request:ok" ) {
                  console.log(2)

               }     // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。 
           }
       ); 
    }

  }

};
</script>

<style lang="css">
  .page-order-detail{
    padding-bottom:50px;
  }
</style>


