<template>
  <div class="page-process">

    <mt-header title="登录">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>

    <div class="process-group userinfo-mint">
      <mt-field class="mint-tel" placeholder="手机号码" v-model="telphone" type="tel"></mt-field>
      <mt-field class="mint-code" placeholder="输入验证码" v-model="verifyCode">
        <mt-button size="small" :disabled="wait!=60" class="captcha-button" type="primary" @click.native="handleCode">
          {{btnText}}
        </mt-button>
      </mt-field>
    </div>

    <div class="process-group process-handle">
      <mt-button size="large" @click.native="handleLogin" type="primary">登录</mt-button>
    </div>

  </div>
</template>

<script>
import ajaxUrl from '../../common/ajaxUrl.js'
import '../../css/process.css';

export default {
  name: 'page-login',

  components: {

  },

  data () {
    return {
      telphone: '',
      verifyCode: '',
      captcha:'',
      wait: 60,
      btnText:'获取验证码',
      captchaUrl: ajaxUrl.captcha,
      loginUrl: ajaxUrl.login
    }
  },


  methods: {
    back () {
      window.history.back();
    },

    handleCode () {
      let isTel = this._checkTel();

      if(isTel && this.wait == 60){
        this.getCode();
      }
    },

    handleLogin () {
      let isTel = this._checkTel();

      if(isTel){
        if(!this.verifyCode){
          this.$toast("请输入验证码");
        }else{
          this.go2login();
        }
      }
    },

    getCode () {
      this.$http.post(this.captchaUrl, {
        phone: this.telphone
      }).then((res) => {
          let data = res.data;
          if(data.status.code == 0){
            //获取验证码
            this.captcha = data.result.code;

            this.countdown();
          }else if(data.status.code == 502){
            this.$toast('短信验证码发生失败');
          }
      }, (res) => {
          this.$toast('获取验证码失败');
      });
    },

    countdown () {
      if (this.wait == 0) {  
        this.btnText = "获取验证码"; 
        this.wait = 60; 
      }else{
        this.wait--;  
        setTimeout(() => {  
            this.btnText = '重新发送('+this.wait + 's)'; 
            this.countdown();
        }, 1000) ;
      }
    },

    go2login (){

      this.$http.post(this.loginUrl, {
        "phone": this.telphone,
        "code": this.verifyCode
      }).then((res) => {
          let data = res.data;
          if(data){
            if(data.status.code == 0){
              this.userInfo = data.result.user_info;
              //存储用户信息
              let dataString = JSON.stringify(this.userInfo); 
              localStorage.setItem("user-info", dataString);

              //存储token
              localStorage.setItem("token", data.result.token);

              this.go2Next();
            }else if(data.status.code == 503){
              this.$toast('短信验证码错误');
            }
          }
      }, (res) => {
          this.$toast('登录失败');
      });
    },

    go2Next (){
      if (!this.$route.query.refer) {
        this.$router.push({ path: 'home'});
      }else{
        window.history.back();
      }
    },

    _checkTel (){
      if (!this.telphone) {
        this.$toast("手机号码不能为空");
        return false;
      }else if (!/^[0-9]{11}$/.test(this.telphone)) {
        this.$toast("手机号码格式错误");
        return false;
      }else{
        return true;
      }
    }
  }

};
</script>



