<template>
  <div class="page-jobs">

    <mt-header title="热门岗位">
      <!--
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      -->
    </mt-header>

    <nav class="tab-city" ref="tabCity">
      <mt-button @click.native="showPopup('province')" 
                size="large" 
                :class="{'active':popupStatus == 'province' && popupVisible}"
                >
        {{address[0]}}
      </mt-button>
      <span class="tab-line"></span>
      <mt-button @click.native="showPopup('city')" 
                size="large"
                :class="{'active':popupStatus == 'city' && popupVisible}"
                >
        {{address[1]}}
      </mt-button>

      <mt-popup v-model="popupVisible" popup-transition="popup-fade" class="city-popup" :style="{ top: tabBottom + 'px' }">
        <ul>
          <li v-if="popupStatus == 'province'" 
              @click="selectProvince(item)"
              :class="{'active':item.area_code == province}"
              v-for="(item, index) in provinceList">
            {{item.area_name}}
          </li>
          <li v-if="popupStatus == 'city'"  
              @click="selectCity(item)"
              :class="{'active':item.area_code == city}"
              v-for="(item, index) in cityList">
            {{item.area_name}}
          </li>
        </ul>
      </mt-popup>
    </nav>

    <div class="list-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
      <ul class="list-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
        <shop-item 
            v-for="(item, index) in jobs"
            key="index"
            :href="'detail?id='+item.id"
            :image="item.cp_logo"
            :title="item.job_name"
            :salary="'【'+item.price_msg+'】'" 
            :source="item.cp_name"
            :location="item.city"
            :welfare="item.welfare" />
      </ul>
      <p v-show="loading" class="list-infinite-loading">
        <mt-spinner type="fading-circle"></mt-spinner>
        加载中...
      </p>
      <p v-if="isEnd && !loading" class="no-more">—— 没有更多了 ——</p>
    </div>
    
  </div>
</template>

<script>
import shopItem from '../../common/shopItem.vue'
import address from '../../common/address.js'
import Util from '../../common/Util'
import '../../css/city.css';

export default {
  name: 'page-jobs',

  components: {
      shopItem
  },

  data() {
    return {
      provinceList:[],
      cityList:[],
      address: [],
      province: '',
      city: '',
      popupVisible: false,
      popupStatus: 'province',
      tabBottom: 0,
      list: [],
      loading: false,
      page: 1,
      isEnd: false,
      allLoaded: false,
      wrapperHeight: 0,
      jobApi: 'http://123.206.221.113:8011/api/recruit/hotjob'
    };
  },

  created() {
    this.$indicator.open();

    let loc = localStorage.getItem("user-location");

    if(loc && loc.indexOf(',') != -1){
      this.address = loc.split(',');

      let province = this.address[0];
      let city = this.address[1];
      this.initLocation(province,city);

    } else {
      let that = this;
      let geolocation = new BMap.Geolocation();

      geolocation.getCurrentPosition(function (r) {
        if (this.getStatus() == BMAP_STATUS_SUCCESS) {
          let province = r.address.province;
          let city = r.address.city;

          that.address = [province, city];

          //存储
          localStorage.setItem("user-location", province+','+city);
        }else{
          that.address = ["北京", "北京市"]
        }

        that.initLocation(that.address[0],that.address[1]);
      });
    }
  },

  mounted() {
    setTimeout(() => {
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;

      this.tabBottom = this.$refs.tabCity.getBoundingClientRect().bottom;
    }, 100);
  },

  computed: {
      jobs() {
        return this.list.map((item, index) => {

          item.welfare = item.tags.join(" ");

          return item
        })
      }
  },

  watch: {
    city: function (val, oldVal) {
      this.list = [];
      this.page = 1;
      this.isEnd = false;
      this.loading = false;

      this.getData(false);
    }
  },

  methods: {
    initLocation (province, city) {
      this.provinceList = address['province'];
      //省
      this.provinceList.forEach((item) => {
          if(item['area_name'] == province){
              this.province = item['area_code'];
              this.cityList = address['city'][this.province];
          }
      });

      //市区
      this.cityList.forEach((item) => {
          if(item['area_name'] == city){
            this.city = item['area_code'];
          }
      });
    },

    getData (loadMore) {
      let query = {
        "province": this.province,
        "city": this.city,
        "page": this.page
      };
      this.$http.get(this.jobApi, {
        params: query
      }).then((res) => {
          let data = res.data;
          if(data.status.code == 0){
            let result = data.result;
            if(!result.list){
              this.$toast('返回数据出错');
            }

            if(!loadMore){
                this.list = result.list;
            }else{
              result.list.forEach((item) => {
                  this.list.push(item);
              });
            }

            this.page ++;
            this.isEnd = result.pages.is_end;

          }else{
            this.isEnd = true;
          }
          
          this.$indicator.close();
      }, (response) => {
          this.$toast('加载内容失败');
          this.$indicator.close();
      });
    },

    showPopup (type) {
      this.popupStatus = type;  //切换省、城市
      
      this.popupVisible = true;
    },
    
    selectProvince (item) {
      this.province = item['area_code'];

      this.cityList = address['city'][this.province]; //更新市列表
      this.city = this.cityList[0]['area_code'];

      this.address = [item['area_name'], this.cityList[0]['area_name']];

      this.popupVisible = false;
    },

    selectCity (item) {
      this.city = item['area_code'];
      this.address[1] = item['area_name'];

      this.popupVisible = false;
    },

    loadMore() {
      if (this.isEnd || this.loading || this.page == 1)  return false;

      this.loading = true;
      setTimeout(() => {
          this.getData(true);
          this.loading = false;
      }, 1500);
    }
  }
  
};
</script>



