<template>

  <div class="common-title">
    <i class="common-icon" :class="iconClass"></i>
    <span class="common-title-text">{{text}}</span>
  </div>

</template>

<script>

  export default {
    name: 'common-title',

    props: {
      iconClass: {
        type: String,
        default: ''
      },
      text: {
        type: String,
        default: 'middle'
      }
    }

  };
</script>

<style lang="css">

  .common-title{
    height: 44px;
    padding:0 10px;
    font-size: 16px;
    display: block;
    border-bottom:1px solid #E9E9E9;
  }
  
  .common-title-text{
    display: inline-block;
    vertical-align: middle;
    max-width: 85%;
    height: 40px;
    line-height: 46px;
    color:#1E1E1E;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  
</style>



