<template>
  <div class="page-apply">

    <mt-header title="应聘报名申请">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>

    <div class="apply-city">
      <h3 >报名城市分区</h3>
      <div>
        <p v-for="(item, index) in areaList">
          【{{item.name}} 】：
          <span v-for="(city, index) in item.citys">
            {{city.name}}
            <i v-show="index < item.citys.length - 1"> 、</i>
          </span>
        </p>
      </div>
    </div>

    <div class="apply-group">
      <h3 class="group-hd">所在地区及城市</h3>
      <div class="group-bd">
        <div class="select-box">
          <select name="" class="dropdown" v-model="area">
            <option value="">请选择</option>
            <option :value="item.value" v-for="(item, index) in areaList">{{item.name}}</option>
          </select>
          <b class="dropdown-bg"></b>
          <i class="dropdown-trigger"></i>
        </div>

        <div class="select-box">
          <select name="" class="dropdown" v-model="city">
            <option value="">请选择</option>
            <option :value="item.value" v-for="(item, index) in cityList">{{item.name}}</option>
          </select>
          <b class="dropdown-bg"></b>
          <i class="dropdown-trigger"></i>
        </div>
      </div>
    </div>

    <div class="apply-group">
      <h3 class="group-hd">员工类别</h3>
      <div class="group-bd">
        <mt-radio
            align="right"
            class="sex-mint"
            v-model="applyType"
            :options="jobType" />
      </div>
    </div>

    <div class="apply-group">
      <h3 class="group-hd">姓名</h3>
      <div class="group-bd userinfo-mint apply-mint">
          <mt-field class="mint-username" label=" " v-model="username" placeholder="请填写姓名">
          </mt-field>
      </div>
    </div>

    <div class="apply-group">
      <h3 class="group-hd">性别</h3>
      <div class="group-bd">
        <mt-radio
            align="right"
            class="sex-mint"
            v-model="sexVal"
            :options="sex" />
      </div>
    </div>

    <div class="apply-group">
      <h3 class="group-hd">年龄 <span>（年龄限制16-45周岁）</span></h3>
      <div class="group-bd apply-mint">
          <mt-field v-model="age" placeholder="请填写年龄" type="tel"></mt-field>
      </div>
    </div>

    <div class="apply-group">
      <h3 class="group-hd">手机号</h3>
      <div class="group-bd userinfo-mint apply-mint">
        <mt-field class="mint-telphone" label=" " v-model="telphone" placeholder="请填写手机号" type="tel"></mt-field>
      </div>
    </div>

    <div class="apply-group">
      <h3 class="group-hd">学历</h3>
      <div class="group-bd">
        <div class="select-box">
          <select name="" class="dropdown" v-model="edu">
            <option value="">请选择</option>
            <option :value="item.value" v-for="(item, index) in eduList">{{item.name}}</option>
          </select>
          <b class="dropdown-bg"></b>
          <i class="dropdown-trigger"></i>
        </div>
      </div>
    </div>
    
    <div class="apply-group">
      <h3 class="group-hd">预计到岗时间</h3>
      <div class="group-bd userinfo-mint apply-mint">
          <mt-field class="mint-date" label=" " v-model="date" placeholder="年-月-日" type="date"></mt-field>
      </div>
    </div>

    <div class="apply-group process-handle">
      <mt-button size="large" @click.native="handleApply" type="primary">提交</mt-button>
    </div>

  </div>
</template>

<script>
import commonTitle from '../../common/title.vue'
import mixBox from '../../common/mixBox.vue'
import address from '../../common/applyAddress.js'
import Util from '../../common/Util'

export default {
  name: 'page-apply',

  components: {
      commonTitle,
      mixBox
  },

  data() {
    return {
      applyType: '1',
      sexVal: '1',
      telphone: '',
      username: '',
      age: '',
      date: '',
      area:'',
      city:'',
      edu:'',
      cityList:[],
      applyApi: 'http://123.206.221.113:8011/api/recruit/jobdetail'
    };
  },


  mounted() {

  },

  watch: {
    area: function (val, oldVal) {

      this.areaList.forEach((item) => {
          if(item['value'] == val){
              this.cityList = item['citys'];
              this.city = "";
          }
      });
    }
  },

  created() {
    this.jobType = [
      {
        label: '正式工',
        value: '1'
      },
      {
        label: '钟点工',
        value: '2'
      },
      {
        label: '实习生',
        value: '3'
      }
    ];
    this.sex = [
      {
        label: '男',value: '1'
      },
      {
        label: '女',value: '2'
      }
    ];
    this.eduList = [
      {
        "name":"初中及以下", value: "1"
      },
      {
        "name":"高中/中专", value: "2"
      },
      {
        "name":"大专", value: "3"
      },
      {
        "name":"本科及以上", value: "4"
      }
    ]

    this.areaList = address;
  },

  methods:{
    back () {
        window.history.back();
    },

    handleApply (){
      
    }
  }
};
</script>

<style lang="css">
  .page-apply{
    background: #f7f7f7;
  }
  .apply-city{
    margin-bottom:20px;
    padding:15px;
    border-bottom: 1px solid #E9E9E9;
  }
  .apply-city h3{
    height: 30px;
    font-size: 15px;
    font-weight: 400;
    color:#f00;
  }
  .apply-city p{
    margin-bottom: 6px;
    font-size:15px;
  }
  .apply-group{
    margin: 10px 0 20px;
  }
  .apply-group .group-hd{
    padding: 0 15px;
    font-size: 15px;
    font-weight: 400;
    color: #828282;
  }
  .apply-group .group-hd span{
    font-size:12px;
    color: #999;
  }
  .apply-group .group-bd{
    margin: 10px 10px;
  }
  .apply-group .group-bd p{
    padding: 5px 10px;
  }

  .apply-mint .mint-cell-wrapper{
    background: none;
    border: 1px solid #C3C9D0;
    border-radius: 2px;
  }
  .sex-mint{
    border: 1px solid #C3C9D0;
    border-radius: 2px;
  }
  .sex-mint .mint-radiolist-title{
    display:none;
  }
  .sex-mint .mint-cell:nth-child(0) .mint-cell-wrapper{
    background: none;
  }
  .sex-mint .mint-cell:last-child{
    background: #fff;
  } 

  .select-box{
    position: relative;
    width: 100%;
    height: 50px;
    margin-bottom:10px;
  }
  .dropdown{
    padding-right: 24px;
    vertical-align: top;
    width: 99%;
    height: 48px;
    position: absolute;
    top: 1;
    left: 1;
    bottom: 0;
    z-index: 3;
    font-size:16px;
    -webkit-appearance: none;
    color:#333;
    padding-left:15px;
    background: none;
    border: 0;
  }
  .select-box .dropdown-bg{
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    border: 1px solid #C3C9D0;
    border-radius: 2px;
    background: #fff;
    z-index: 1;
  }
  .select-box .dropdown-trigger {
    position: absolute;
    top: 22px;
    right: 10px;
    margin-top: -2px;
    display: inline-block;
    width: 0;
    height: 0;
    border: 6px solid transparent;
    border-top: 8px solid #555;
    border-bottom-width: 0;
    z-index: 2;
}

  .apply-group .mint-field .mint-cell-title{
    width: 0;
    padding-left: 45px;
  }
  .apply-group .mint-field .mint-field-core{
    height:40px;
    color:#333;
  }
  
  .process-handle{
    border-top: 1px solid #E9E9E9;
    padding:30px 10px;
  }
  .process-handle button{
    background: #0977C5;
  }
  
</style>


