<template>
  <li class="shop-item">
    <router-link :to="href">
    <div class="mix-box">
      <img class="mix-pic" v-lazy="image" />
      <div class="mix-info">
        <h3 class="mix-title">{{ title }} <span class="orange">{{ salary }}</span></h3>
        <p class="mix-desc">{{ source }}</p>
      </div> 
    </div>

    <ul class="item-foot">
      <li>
        <i class="common-icon icon-loc-gray"></i>
        <span>{{location}}</span>
      </li>
      <li>
        <i class="common-icon icon-gift-gray"></i>
        <span v-for="(item, index) in welfare.split(',')">{{item}}</span>
      </li>
    </ul>
    </router-link>
  </li>
</template>

<script>

  export default {
    name: 'shop-item',

    props: {
      image: {
        type: String,
        default: ''
      },
      title: {
        type: String,
        default: ''
      },
      salary: {
        type: String,
        default: ''
      },
      source: {
        type: String,
        default: ''
      },
      location: {
        type: String,
        default: ''
      },
      welfare: {
        type: String,
        default:''
      },
      href: {
        type: String,
        default:''
      }
    }

  };
</script>

<style lang="css">

  .shop-item{
    margin:10px 0;
    padding:20px 15px;
    background: #fff;
    border-top: 1px solid #E9E9E9;
    border-bottom: 1px solid #E9E9E9;
  }

  .shop-item .item-foot{
    margin:20px 0 0 0;
    padding: 15px 0 0 0;
    border-top: 1px dashed #E9E9E9;
    display:flex;
  }

  .shop-item .item-foot li{
    margin-right:20px;
    font-size:15px;
    color:#828282;
  }
  .shop-item .item-foot li span{
    margin:0 3px;
    vertical-align: middle;
  }

  
</style>



