<template>

  <li class="job-item">
    <router-link :to="href">
      <h3 class="job-title">{{ title }} </h3>
      <ul class="job-info">
        <li>
          <i class="common-icon icon-loc-gray"></i>
          <span>{{location}}</span>
        </li>
        <li>
          <i class="common-icon icon-time-gray"></i>
          <span>{{time}}</span>
        </li>
      </ul>
      <div class="job-salary">
        <span class="orange money">{{ salary }}</span><span class="orange">元/天</span>
      </div>
    </router-link>
  </li>

</template>

<script>

  export default {
    name: 'job-item',

    props: {
      title: {
        type: String,
        default: ''
      },
      salary: {
        type: String,
        default: ''
      },

      location: {
        type: String,
        default: ''
      },
      time: {
        type: String,
        default:''
      },
      href: {
        type: String,
        default:''
      }
    }

  };
</script>

<style lang="css">
  .job-item{
    margin:10px 0;
    padding:15px;
    background: #FFFFFF;
    border-top: 1px solid #E9E9E9;
    border-bottom: 1px solid #E9E9E9;
  }

  .job-title{
    margin:10px 0;
    height:16px;
    line-height:16px;
    font-size: 17px;
    color: #1E1E1E;
  }

  .job-info{
    display:flex;
  }

  .job-item .job-info li{
    margin-right:20px;
    font-size:15px;
    color:#828282;
  }
  .job-item .job-info li span{
    margin:0 2px;
    vertical-align: middle;
  }

  .job-salary{
    margin-top:10px;
    font-size:14px;
  }
  .job-salary .money{
    font-size:17px;
  }
  
</style>



