<template>
  <div class="page-order-place">

    <mt-header title="实惠住宿">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>

    <div class="box">
      <common-title iconClass="icon-house" text="宿舍信息"> </common-title>
      <div class="box-desc">
        <ul class="list-info">
          <li>
            <label>住 宿 费：</label>
            <span v-if="build.total_amount" class="orange"> ￥{{build.total_amount}} </span>
          </li>
          <li>
            <label>性     别：</label>
            <span v-if="build.sex == 1"> 男 </span>
            <span v-else-if="build.sex == 2"> 女 </span>
          </li>
          <li>
            <label>房间信息：</label>
            <span> {{build.room_name}} </span>
          </li>
          <li>
            <label>床位信息：</label>
            <span> {{build.berth_name}}</span>
          </li>
          <li>
            <label>入住时间：</label>
            <span> {{build.date_range}} </span>
          </li>
          <li>
            <label>宿舍地点：</label>
            <span> {{build.building_address}} </span>
          </li> 
        </ul>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-user" text="个人信息"> </common-title>
      <div class="box-desc">
        <ul class="list-info">
          <li>
            <label>入 住 人：</label>
            <span> {{user.predetermine_name}} </span>
          </li>
          <li>
            <label>手机号码：</label>
            <span> {{user.predetermine_phone}} </span>
          </li>
          <li>
            <label>身份证号：</label>
            <span> {{user.predetermine_id_card}} </span>
          </li>
          <li>
            <label>入住类型：</label>
            <span v-if="user.predetermine_type == 1"> 培训入住 </span>
            <span v-else-if="user.predetermine_type == 2"> 工作入住 </span>
            <span v-else> 其他 </span>
          </li>
        </ul>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-attention" text="订单信息"> </common-title>
      <div class="box-desc">
        <p v-html="order.order_reminder">{{order.order_reminder}}</p>
      </div>
    </div>

    <div class="fix-bottom" v-if="order.order_id">
      <mt-button size="large" type="primary" @click.native="handlePay">确认支付</mt-button>
    </div>
  </div>
</template>

<script>
import commonTitle from '../../common/title.vue'
import ajaxUrl from '../../common/ajaxUrl'
import Util from '../../common/Util'

export default {
  name: 'page-order-place',

  components: {
      commonTitle
  },

  data() {
    return {
      build:{},
      user:{},
      order:{},
      bids: '',
      creatOrderApi: ajaxUrl.create_order
    };
  },

  created() {
    this.$indicator.open();

    //获取ID
    this.bids = this.$route.query.bids;
    setTimeout(() => {
        this.bids != '' && this.getData();

        this.$indicator.close();
    }, 500);
  },

  methods: {
    back () {
      if(!this.order.order_id){
        this.$router.go(-3);  //重新预定
      }else{
        this.$router.push({ path: '/order/list', query: { refer: 'place'}})
      }
    },

    getData () {
      this.$http.post(this.creatOrderApi, {
        'berth_id': this.bids.split(','),
        'token': Util.getToken()
      }).then((res) => {
        let data = res.data;
        if(data.status.code == 0){
          let result = data.result;

          if(result){
            this.build = result.building;
            this.user = result.predeterminer;
            this.order = result.order;
          }
        }else if(data.status.code == 504){
          this.$toast(data.status.msg);
          setTimeout(() => {
            this.goLogin();
          }, 2000);
        }else if(data.status.code == 500){
          //提示重新预定
          //this.goBack();
        }
      }, (res) => {
          this.$toast('加载内容失败');
      });
    },

    goBack () {
      this.$messagebox({
        title: '',
        message: '订单信息已失效，请重新预定',
        type:'alert',
        showCancelButton: false,
        confirmButtonText: '好的'
      }).then(action => {
        console.log(action);
      });
    },

    goLogin () {
      this.$router.push({ path: '/user/login', query: { refer: 'place'}})
    },

    handlePay () {
      //todo
      console.log('去支付')
    }
  }

};
</script>

<style lang="css">
  .page-order-place{
    padding-bottom:50px;
  }
</style>


