<template>
  <div class="page-booking">

    <mt-header title="实惠住宿">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>
    
    <div class="userinfo-group">
      <h3 class="group-hd">预定人信息</h3>
      <div class="group-bd userinfo-mint">
        <mt-field class="mint-username" label="入 住 人：" v-model="username" placeholder="入住人">
        </mt-field>
        <mt-field class="mint-telphone" label="手机号码：" v-model="telphone" placeholder="手机号码" type="tel"></mt-field>
        <mt-field class="mint-identity" label="身份证号：" v-model="identity" placeholder="身份证号"></mt-field>
      </div>
    </div>

    <div class="userinfo-group">
      <h3 class="group-hd">选择入住类型</h3>
      <div class="group-bd">
        <mt-radio
            align="right"
            class="userinfo-mint"
            v-model="checkType"
            :options="options" />
      </div>
    </div>

    <div class="xieyi-group">
      <label for="xieyi">
        <input id="xieyi" class="mint-radio-input" v-model="xieyi"  type="checkbox" />
        <span class="mint-radio-core"></span>
        <span class="xieyi-link">我已阅读 <router-link to="/dormitory/xieyi">《免责协议》</router-link></span>
      </label>
    </div>

    <div class="fix-bottom">
      <mt-button @click.native="handleNext" size="large" type="primary">下一步</mt-button>
    </div>
    
  </div>
</template>

<script>
import '../../css/process.css';
import '../../css/booking.css';
import ajaxUrl from '../../common/ajaxUrl'
import Util from '../../common/Util'

export default {
  name: 'page-userinfo',

  components: {
  },
  
  data() {
    return {
      username: '',
      telphone: '',
      identity: '',
      checkType: '1',
      xieyi: false,
      apiUrl: ajaxUrl.build_room
    };
  },

  created() {
    this.options = [
      {
        label: '培训入住',
        value: '1'
      },
      {
        label: '工作入住',
        value: '2'
      },
      {
        label: '其他',
        value: '3'
      }
    ];
  },

  methods: {
    back () {
      window.history.back();
    },
    
    handleNext () {
      let isLogin = Util.isLogin();
      if(!isLogin){
        this.$router.push({ path: '/user/login', query: { refer: 'userInfo'}})
        return false;
      }

      this.go2Next();
    },

    go2Next (){
      let isCheck = this._checkForm();
      if(isCheck){
        let params ={
          name: encodeURIComponent(this.username),
          tel: this.telphone,
          identity: this.identity,
          type: this.checkType
        }
        this.$router.push({ path: 'house', query: params})  
      }
    },

    _checkForm (){
      let regTel = /^[0-9]{11}$/;
      let regID = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;  

      if (!this.username){
        this.$toast("入住人不能为空");
        return false;
      } else if (!this.telphone) {
        this.$toast("手机号码不能为空");
        return false;
      } else if (!regTel.test(this.telphone)) {
        this.$toast("手机号码格式错误");
        return false;
      } else if (!this.identity) {
        this.$toast("身份证号不能为空");
        return false;
      } else if (!regID.test(this.identity)) {
        this.$toast("身份证输入不合法");
        return false;
      } else if(!this.xieyi){
        this.$toast("请同意《免责协议》");
        return false;
      } else {
        return true;
      }
    }
  }
};
</script>

<style lang="css">
  .userinfo-group {
    margin: 0 0 10px;
  }

  .userinfo-group .group-hd{
    margin: 10px 0;
    padding: 0 15px;
    font-size: 15px;
    font-weight: 400;
    color: #828282;
  }

  .xieyi-group{
    margin-bottom: 40px;
    padding-right: 20px;
    line-height: 30px;
    text-align: right;
  }
  .xieyi-link{
    font-size:14px;
    padding-top:5px;
  }
  .xieyi-link a{
    color: #0977C5;
  }

</style>


