<template>
  <div class="page-order-list">

    <mt-header title="我的订单">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>


    <mt-navbar class="page-part" v-model="selected">
      <mt-tab-item id="1">待支付</mt-tab-item>
      <mt-tab-item id="2">已支付</mt-tab-item>
      <mt-tab-item id="3">已取消</mt-tab-item>
    </mt-navbar>

    <mt-tab-container v-model="selected" ref="wrapper">

      <mt-tab-container-item id="1">
        <div class="list-infinite-wrapper" :style="{ height: wrapperHeight + 'px' }">
          <ul class="list-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
            <order-item 
                    v-for="(item, index) in list1"
                    key="index"
                    :href="'detail?id='+item.order_id"
                    :hotel="item.building_name"
                    :room="item.room_name" 
                    :time="item.date_range"
                    :price="'￥'+item.total_amount"
                    btnTxt="去支付" />
          </ul>
          <p v-if="loading" class="list-infinite-loading">
            <mt-spinner type="fading-circle"></mt-spinner>
            加载中...
          </p>
          <p v-if="isEnd && !loading" class="no-more">—— 没有更多了 ——</p>
        </div>
      </mt-tab-container-item>

      <mt-tab-container-item id="2">
        <div class="list-infinite-wrapper" :style="{ height: wrapperHeight + 'px' }">
          <ul class="list-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
            <order-item 
                    v-for="(item, index) in list2"
                    key="index"
                    :href="'detail?id='+item.order_id"
                    :hotel="item.building_name"
                    :room="item.room_name" 
                    :time="item.date_range"
                    :price="'￥'+item.total_amount"
                    btnTxt="续订" />
          </ul>
          <p v-if="loading" class="list-infinite-loading">
            <mt-spinner type="fading-circle"></mt-spinner>
            加载中...
          </p>
          <p v-if="isEnd && !loading" class="no-more">—— 没有更多了 ——</p>
        </div>
      </mt-tab-container-item>

      <mt-tab-container-item id="3">
        <div class="list-infinite-wrapper" :style="{ height: wrapperHeight + 'px' }">
          <ul class="list-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
            <order-item 
                    v-for="(item, index) in list3"
                    key="index"
                    :href="'detail?id='+item.order_id"
                    :hotel="item.building_name"
                    :room="item.room_name" 
                    :time="item.date_range"
                    :price="'￥'+item.total_amount"
                    btnTxt="查看" />
          </ul>
          <p v-if="loading" class="list-infinite-loading">
            <mt-spinner type="fading-circle"></mt-spinner>
            加载中...
          </p>
          <p v-if="isEnd && !loading" class="no-more">—— 没有更多了 ——</p>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>

<script>
import orderItem from '../../common/orderItem.vue'
import ajaxUrl from '../../common/ajaxUrl'
import Util from '../../common/Util'

export default {
  name: 'page-order-list',

  components: {
      orderItem
  },

  data() {
    return {
      selected: '1',
      list1: [],
      list2: [],
      list3: [],
      loading: false,
      page: 1,
      isEnd: false,
      allLoaded: false,
      wrapperHeight: 0,
      orderApi: ajaxUrl.order_list
    };
  },

  created() {
    this.$indicator.open();

    setTimeout(() => {
        this.getData(false);

        this.$indicator.close();
    }, 500);
  },

  mounted() {
    let clientH = document.documentElement.clientHeight;
    this.wrapperHeight = clientH - this.$refs.wrapper.$el.getBoundingClientRect().top;

  },

  watch: {
    selected: function (val, oldVal) {
      this.list1 = [];
      this.list2 = [];
      this.list3 = [];
      this.page = 1;
      this.isEnd = false;
      this.loading = false;

      console.log(val, oldVal)
      this.getData(false);
    }
  },

  methods: {
    back () {
      window.history.back()
    },

    getData(loadMore) {
      this.$http.post(this.orderApi, {
        'order_status': this.selected,
        'page': this.page,
        'per_page': '10',
        'token': Util.getToken()
      }).then((res) => {
        let data = res.data;
        if(data.status.code == 0){
          if(!data.result.orders){
            this.$toast('返回数据出错');
          }

          this.page ++;
          this.formatData(loadMore, data.result);
        
        }else if(data.status.code == 504){
          this.$toast(data.status.msg);
          setTimeout(() => {
            this.goLogin();
          }, 2000);
        }
      }, (res) => {
        this.$toast('加载内容失败');
      });
    },

    formatData(loadMore, data) {
      this.isEnd = data.pages.is_end;

      if(!loadMore){
        if(this.selected == '1'){
          this.list1 = data.orders;
        }else if(this.selected == '2'){
          this.list2 = data.orders;
        }else{
          this.list3 = data.orders;
        }
      }else{
        data.orders.forEach((item) => {
          if(this.selected == '1'){
            this.list1.push(item);
          }else if(this.selected == '2'){
            this.list2.push(item);
          }else{
            this.list3.push(item);
          }
        });
      }
    },

    loadMore(e) {
      if (this.isEnd || this.loading || this.page == 1)  return false;

      this.loading = true;
      setTimeout(() => {
          this.getData(true);
          this.loading = false;
      }, 1500);
    },

    goLogin (){
      this.$router.push({ path: '/user/login', query: { refer: 'order-list'}})
    }
  }
  
};
</script>

<style lang="css">
  .page-order-list{

  }

  .mint-navbar{
    border: 1px solid #E9E9E9;
  }
  .mint-navbar .mint-tab-item{
    padding:15px 0;
  }
  .mint-navbar .mint-tab-item.is-selected{
    color: #0977C5;
    border-color:#0977C5;
    margin-bottom:0;
  }
  .mint-tab-item-label {
    font-size: 16px;
  }
</style>


