<template>
  <div class="page-hotel">

    <mt-header title="实惠住宿">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>

    <mt-swipe :auto="4000">
      <mt-swipe-item v-for="(item, i) in result.building_photos" key="i">
        <img :src="item" />
      </mt-swipe-item>
    </mt-swipe>

    <div class="box hotel-info">
      <div>
        <h3 class="hotel-name">
          {{result.building_name}} 
          <span class="hotel-tag" v-for="tag in result.building_tag" key="j">
            {{tag}}
          </span>
        </h3>
        <p class="hotel-extra-info">{{result.full_address}} </p>
        <p class="hotel-extra-info">{{result.contact_person}}   {{result.contact_phone}} </p>
      </div>
      <div class="hotel-tel">
        <a class="common-icon icon-call" @click="handleTell"></a>
      </div>
    </div>

    <div class="box" v-if="result.building_description" >
      <common-title iconClass="icon-house" text="宿舍简介"> </common-title>
      <div class="box-desc">
        <p v-html="result.building_description">{{result.building_description}}</p>
      </div>
    </div>

    <div class="box check-info">
      <h5>入住选择</h5>
      <div class="check-sex">
        <label>性别：</label>
        <span v-for="(item, index) in sex"
            key="index"
            :class="{'on':item.value == sexVal}"
            @click="handleSelect(item)">
              {{item.label}}
        </span>
      </div>
      
      <div class="check-date" @click.stop="handleDate">
        <div class="check-in">入住日期 <span>{{begin}}</span></div>
        <div class="check-day">共{{count}}天</div>
        <div class="check-out">退房日期 <span>{{end}}</span></div>
      </div>
    </div>

    <div class="box booking">
      <ul>
        <li v-for="(item, index) in result.building_room_category">
          <div class="booking-room">
            {{item.category_name}} 
            <span>{{item.category_desc}}</span>
          </div>
          <div class="booking-price orange"> <span>{{item.category_price}}</span>元/床</div>
          <mt-button @click.native="handleBooking(item)" plain size="small">
            预定
          </mt-button>
        </li>
        
      </ul>
    </div>
    
  </div>
</template>

<script>
import commonTitle from '../../common/title.vue'
import ajaxUrl from '../../common/ajaxUrl'
import Util from '../../common/Util'

export default {
  name: 'page-hotel',

  components: {
    commonTitle
  },
  
  data() {
    return {
      begin: '',
      end:'',
      count: 1,
      start_time: '',
      end_time: '',
      sexVal: '1',
      sex: [
        {
          label: '男',value: '1'
        },
        {
          label: '女',value: '2'
        }
      ],
      bid: '',
      result: {},
      infoApi: ajaxUrl.build_info,
      reserveApi: ajaxUrl.reserve
    };
  },

  created() {
    let begin = Util.getToday();
    let end = Util.getTomorrow();

    let data = localStorage.getItem("booking-date");
    if(data && data != ""){
      let date = JSON.parse(data);

      if(Util.isObject(date)){
        begin = date.begin;
        end = date.end;
        this.count = date.count;
      }
    }

    //界面显示
    this.begin = begin[1]+"月"+begin[2]+"日";
    this.end = end[1]+"月"+end[2]+"日";

    //后端参数
    this.start_time = begin.join("-");
    this.end_time = end.join("-");

    if(localStorage.getItem("booking-sex")){
      this.sexVal = localStorage.getItem("booking-sex");
    }

    
    //获取ID
    this.bid = this.$route.query.bid;
    if(!this.bid){
        this.$toast('没有住宿ID');
        return false;
    }

    this.$indicator.open();
    setTimeout(() => {
        this.getData();

        this.$indicator.close();
    }, 500);
  },


  destroyed () {
    localStorage.setItem("booking-date", ""); //清除日期存储
  },

  methods: {
    back () {
      window.history.back();
    },

    getData () {
      this.$http.post(this.infoApi, {
        'building_id': this.bid
      }).then((res) => {
          let data = res.data;
          if(data && data.status.code == 0){
            this.result = data.result;
          }
      }, (res) => {
          this.$toast('加载内容失败');
      });
    },

    handleDate () {
      this.$router.push({ path: 'date', query: { refer: 'hotel' }})
    },

    handleSelect (item) {
      this.sexVal = item.value;
      localStorage.setItem("booking-sex", item.value);  //存储性别
    },

    handleTell () {
      location.href = 'tel:'+this.result['contact_phone'];
    },

    handleBooking (item) {
      let isLogin = Util.isLogin();
      if(!isLogin){
        this.goLogin();
        return false;
      }

      this.$http.post(this.reserveApi, {
        'building_id': this.bid,
        'category_id': item._id,
        'start_time': this.start_time,
        'end_time': this.end_time,
        'sex': this.sexVal,
        'token': Util.getToken()
      }).then((res) => {
          let data = res.data;
          
          if(data.status.code == 0){
            this.$toast('预定成功，填写预定人信息');
            
            this.go2Next(item);
          }else if(data.status.code == 504){
            this.$toast(data.status.msg);
            setTimeout(() => {
              this.goLogin();
            }, 2000);
          }
      }, (res) => {
          this.$toast('预定房间失败，重新尝试');
      });
    },

    goLogin (){
      this.$router.push({ path: '/user/login', query: { refer: 'hotel'}})
    },

    go2Next (item){
      let data = {
          price: item.category_price,
          name: item.category_name,
          days: this.count,
          bid: this.bid
      };

      let dataString = JSON.stringify(data);
      localStorage.setItem("booking-house", dataString);  //存储房间信息

      setTimeout(() => {
        this.$router.push({ path: 'userInfo'});
      }, 2000);
    }

  }
};
</script>

<style lang="css">
  .mint-swipe {
    height: 170px;
    overflow: hidden;
  }
  .mint-swipe-item img{
    width: 100%;
  }

  .hotel-info{
    position: relative;
    margin-bottom: 10px;
    min-height:90px;
    padding: 10px 15px;
    background: #fff;
  }

  .hotel-info .hotel-tel {
    position: absolute;
    right: 10px;
    top: 20px;
    width: 60px;
    height: 70px;
    border-left: 1px solid #E9E9E9;
  }

  .hotel-info .icon-call{
    width: 60px;
    height: 70px;
    background: url(../../../images/call_icon@3x.png) 15px 20px no-repeat;
    background-size: 26px auto;
  }


  .check-info{
    margin-top: -11px;
  }
  .check-info h5{
    margin: 0;
    padding: 10px 15px;
    background: #f0f0f0;
    font-size: 15px;
    color: #828282;
    font-weight: 400;
  }
  .check-info .check-sex{
    display: flex;
    align-items: center;
    padding: 15px;
    font-size: 15px;
    color: #828282;
    border-bottom: 1px solid #E9E9E9;
  }
  .check-info .check-sex span{
    margin: 0 6px;
    padding: 6px 25px;
    background: #fff;
    border: 1px solid #D7D7D7;
    border-radius: 4px;
  }
  .check-info .check-sex span.on{
    background: #0977C5;
    border: 1px solid #0977C5;
    color: #fff;
  }

  .booking>ul>li{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    border-bottom: 1px solid #E9E9E9;
  }
  .booking-room{
    width: 50%;
    line-height: 24px;
    font-size: 17px;
    color: #1E1E1E;
  }
  .booking-room span{
    display: block;
    font-size: 15px;
    color: #828282;
  }
  .booking-price{
    width: 26%;
    font-size: 14px;
    text-align: right;
  }
  .booking-price span{
    font-size: 18px;
  }
  .booking>ul>li> .mint-button{
    padding: 0 15px;
    border: 1px solid #0977C5;
    color: #0977C5;
  }

</style>


