<template>
  <div class="page-jobs">

    <mt-header title="岗位详情">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>

    <div class="box">
      <h3 class="part-time-title">{{data.job_name}}</h3>
      <div class="box-desc">
        <ul class="list-info">
          <li>
            <label class="tl">发布机构：</label>
            <span>{{data.cp_name}}</span>
          </li>
          <li>
            <label class="tl">薪资待遇：</label>
            <span class="orange">{{data.price_msg}}元/时</span>
          </li>
          <li>
            <label class="tl">兼职类型：</label>
            <span>{{data.parttime_job_title}} </span>
          </li>
          <li>
            <label class="tl">招聘人数：</label>
            <span> {{data.num_people}}人</span>
          </li>
          <li>
            <label class="tl">性别要求：</label>
            <span> {{data.sex}} </span>
          </li>
        </ul>
      </div>
    </div>

    <div class="box">
      <ul class="list-info list-claim">
          <li>
            <label class="tl"><i class="common-icon icon-time"></i>工作日期：</label>
            <span>{{data.job_date}}</span>
          </li>
          <li>
            <label class="tl"><i class="common-icon icon-time2"></i>上班时间：</label>
            <span>{{data.work_date}}</span>
          </li>
          <li>
            <label class="tl"><i class="common-icon icon-loc"></i>上班地址：</label>
            <span>{{data.job_address}}</span>
          </li>
        </ul>
    </div>
    

    <div class="box">
      <common-title iconClass="icon-info" text="工作内容"> </common-title>
      <div class="box-desc">
        {{data.job_content}}
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-attention" text="报名须知"> </common-title>
      <p class="box-desc">
        {{data.my_notice}}
      </p>
    </div>

    <div class="box">
      <common-title iconClass="icon-attention" text="公司照片"> </common-title>
      <div class="box-desc" v-if="showPic">
        <mt-swipe :auto="4000">
          <mt-swipe-item v-for="(item, i) in data.job_images" key="i">
            <img :src="item" />
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <div class="box-desc" v-else>无</div>
    </div>

    <div class="fix-bottom fix-signup">
      <mt-button @click.native="handleAsk">
        <img src="../../../images/consultation_online_icon@3x.png" height="20" width="20" slot="icon">
        在线咨询
      </mt-button>
      <mt-button @click.native="handleTell" type="primary">
        <img src="../../../images/consultation_call_icon2@3x.png" height="20" width="20" slot="icon">
        我要报名
      </mt-button>
    </div>
  </div>
</template>

<script>
import commonTitle from '../../common/title.vue'
import mixBox from '../../common/mixBox.vue'
import Util from '../../common/Util'

export default {
  name: 'page-jobs',

  components: {
      commonTitle,
      mixBox
  },

  data() {
    return {
      id:0,
      showPic: false,
      data: {},
      jobDetailApi: 'http://123.206.221.113:8011/api/recruit/jobpartdetail'
    };
  },

  created() {
    //获取ID
    this.id = this.$route.query.id;
    if(!this.id){
        this.$toast('没有招聘信息');
        return false;
    }

    this.$indicator.open();

    setTimeout(() => {
        this.getData();

        this.$indicator.close();
    }, 1000);
    
  },

  methods: {
    back () {
      window.history.back();
    },

    getData () {
      let query = {
        "id": this.id
      };
      this.$http.get(this.jobDetailApi, {
        params: query
      }).then((res) => {
          let data = res.data;
          if(data.status.code == 0){
            this.data = data.result;

            if(this.data.job_images.length > 0) {
              this.showPic = true;
            }
            if(this.data.job_date){
              this.data.job_date = Util.formatDay(parseInt(this.data.job_date));
            }

          }else{
            this.$toast('加载内容失败');
          }
      }, (response) => {
          this.$toast('加载内容失败');
      });
    },

    handleAsk: function() {
      this.$messagebox({
        title: '提交成功',
        message: '微海已经收到您的信息啦～请耐心等待回复哦！',
        type:'alert',
        showCancelButton: false,
        confirmButtonText: '好的'
      }).then(action => {
        console.log(action);
      });
    },

    handleTell: function() {
      location.href = "tel:"+this.data.tel;
    }
  }
};
</script>

<style lang="css">
  .page-jobs{
    padding-bottom:50px;
  }
  .part-time-title{
    margin:0 15px;
    padding:15px 0;
    border-bottom: 1px dashed #D7D7D7;
  }

  .mint-swipe {
    width: 100%;
    height: 160px;
    overflow: hidden;
  }
  .mint-swipe-item img{
    width: 100%;
    height: 100%;
  }

  .fix-signup{
    display:flex;
  }
  .fix-signup .mint-button--normal{
    box-shadow:none;
    flex: 2;
    font-size:16px;
  }
  .fix-signup .mint-button--primary{
    background: #0977C5;
    box-shadow:none;
    flex: 3;
    font-size:16px;
  }

</style>



