const Util = {
    isLogin() {
        let token = this.getToken();

        if(!token) {
            return false;
        }
        return true;
    },

    getToken () {
        let token = localStorage.getItem("token");

        return token;
    },

    getToday () {
        let now = new Date();
        let year = String(now.getFullYear())
        let month = this.zeroPad(now.getMonth()+1)
        let today = this.zeroPad(now.getDate())

        return [year, month, today];
    },

    getTomorrow () {
        let now = new Date();
        let tomorrow = this.zeroPad(now.getDate()+1)
        let today = this.getToday();

        today[2] = tomorrow;

        return today;
    },


    formatDate(now) { 
        return new Date(parseInt(now) * 1000).toLocaleString().replace(/:\d{1,2}$/,' ');
    },

    formatDay(now) { 
        let day = this.formatDate(now);

        return day.split(' ')[0].split('/').join('-');
    },

    //小于10补0
    zeroPad(n){
        return String(n < 10 ? '0' + n : n)
    },

    isObject (val){
      return Object.prototype.toString.call(val) === '[object Object]'
    }
}



export default Util




