<template>
  <div class="page-map">

    <mt-header title="实惠住宿">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header> 

    <nav class="tab-city" ref="tabCity">
      <mt-button @click.native="showPopup('province')" 
                size="large" 
                :class="{'active':popupStatus == 'province' && popupVisible}"
                >
        {{address[0]}}
      </mt-button>
      <span class="tab-line"></span>
      <mt-button @click.native="showPopup('city')" 
                size="large"
                :class="{'active':popupStatus == 'city' && popupVisible}"
                >
        {{address[1]}}
      </mt-button>

      <mt-popup v-model="popupVisible" popup-transition="popup-fade" class="city-popup" :style="{ top: tabBottom + 'px' }">
        <ul>
          <li v-if="popupStatus == 'province'" 
              @click="selectProvince(item)"
              :class="{'active':item.area_code == province}"
              v-for="(item, index) in provinceList">
            {{item.area_name}}
          </li>
          <li v-if="popupStatus == 'city'"  
              @click="selectCity(item)"
              :class="{'active':item.area_code == city}"
              v-for="(item, index) in cityList">
            {{item.area_name}}
          </li>
        </ul>
      </mt-popup>
    </nav>

    <div id="allmap" class="map-wrap" ref="mapWrap" :style="{ height: mapHeight + 'px' }"></div>

    <div class="fix-bottom fix-map">
      <mt-button @click.native="pointVisible = true">
        本地区共有<span>{{buildCount}}</span>个住宿点
      </mt-button>
      <mt-popup v-model="pointVisible" position="bottom" class="hotel-popup">
        <mt-button @click.native="pointVisible = false">
          本地区共有<span>{{buildCount}}</span>个住宿点
        </mt-button>
        <div class="list-hotel-popup">

          <div class="hotel-item" v-for="(item, i) in buildList" key="i" @click="handleHotel(item._id)" >
            <h3 class="hotel-name">
              {{item.building_name}}
              <span class="hotel-tag" v-for="tag in item.building_tag" key="j">
                {{tag}}
              </span>
            </h3>
            <p class="hotel-extra-info">{{item.full_address}}</p>
          </div>
          
        </div>
      </mt-popup>
    </div> 
    
  </div>
</template>


<script>
import address from '../../common/address.js'
import mapOverlay from '../../common/mapOverlay.js'
import ajaxUrl from '../../common/ajaxUrl.js'
import '../../css/city.css';


export default{
  name: 'page-map',

  data : function(){
    return{
      provinceList:[],
      cityList:[],
      address: [],
      province: '',
      city: '',
      cityLocation:'',
      popupVisible: false,
      popupStatus: 'province',
      tabBottom: 0,
      pointVisible: false,
      mapHeight:500,
      buildCount:0,
      buildList:[],
      buildApi: ajaxUrl.build_list
    }
  },

  created() {
    this.$indicator.open();

    let loc = localStorage.getItem("user-location");

    if(loc && loc.indexOf(',') != -1){
      this.address = loc.split(',');

      let province = this.address[0];
      let city = this.address[1];
      this.initLocation(province,city);

    } else {
      let that = this;
      let geolocation = new BMap.Geolocation();

      geolocation.getCurrentPosition(function (r) {
        if (this.getStatus() == BMAP_STATUS_SUCCESS) {
          let province = r.address.province;
          let city = r.address.city;

          that.address = [province, city];
          that.initLocation(province,city);
          
          //存储
          localStorage.setItem("user-location", province+','+city);
        }
      });
    }
  },

  mounted() {
    //初始化地图
    this.map = new BMap.Map("allmap");
    //操作
    this.map.enableScrollWheelZoom(true);
    this.map.enableDoubleClickZoom(true);
    
    setTimeout(() => {
      this.mapHeight = document.documentElement.clientHeight - this.$refs.mapWrap.getBoundingClientRect().top;
      this.tabBottom = this.$refs.tabCity.getBoundingClientRect().bottom;
    }, 100);
  },

  watch: {
    city: function (val, oldVal) {
      this.getData();
    }
  },

  methods: {

    initLocation (province, city) {
      this.provinceList = address['province'];
      //省
      this.provinceList.forEach((item) => {
          if(item['area_name'] == province){
              this.province = item['area_code'];
              this.cityList = address['city'][this.province];
          }
      });

      //市区
      this.cityList.forEach((item) => {
          if(item['area_name'] == city){
            this.city = item['area_code'];
          }
      });
    },

    getData () {
      this.$http.post(this.buildApi, {
        "province": this.province,
        "city": this.city
      }).then((res) => {
          let data = res.data;
          if(data && data.status.code == 0){

            this.buildCount = data.result.count;
            this.buildList = data.result.builds;
            this.cityLocation = data.result.city_lat_lon;

            this.renderMap();
            this.$indicator.close();
          }
      }, (res) => {
          this.$toast('加载内容失败');
          this.$indicator.close();
      });
    },

    showPopup (type) {
      this.popupStatus = type;  //切换省、城市
      
      this.popupVisible = true;
    },
    
    selectProvince (item) {
      this.province = item['area_code'];

      this.cityList = address['city'][this.province]; //更新市列表
      this.city = this.cityList[0]['area_code'];

      this.address = [item['area_name'], this.cityList[0]['area_name']];

      this.popupVisible = false;
    },

    selectCity (item) {
      this.city = item['area_code'];
      this.address[1] = item['area_name'];

      this.popupVisible = false;
    },

    renderMap () {
      let map = this.map;
      //清除overlay
      map.clearOverlays();

      //中心坐标、地图级别
      let city_loc = this.cityLocation.split(',');
      map.centerAndZoom(new BMap.Point(city_loc[0],city_loc[1]), 12);
      
      //标注宿舍
      this.buildList.map( (item) => {
        let id = item._id;
        let name = item.building_name;
        let loc = item.building_lat_lon.split(',');
        
        let overlay = new mapOverlay(new BMap.Point(loc[0],loc[1]), name);
        map.addOverlay(overlay);

        //跳转事件
        overlay.addEventListener("touchend", ev => {
          this.handleHotel(id)
        });
        
      });
    },

    handleHotel (id) {
      this.$router.push({ path: 'hotel', query: { bid: id}})
    }
  }

}
</script>

<style lang="css">
  .fix-map .mint-button--normal{
    width:100%;
    background:url(../../../images/down_icon@3x.png) #fff 80% 20px no-repeat;
    background-size: 12px auto;
  }

  .fix-map .mint-button--normal span{
    color: #0977C5;
  }
  .fix-map .mint-button-text{
    margin-left:-20px;
  }

  .map-wrap{
    width:100%;
  }
  .list-hotel-popup{
    margin-top: 1px;
    max-height: 300px;
    overflow-y: scroll;
  }
  .list-hotel-popup .hotel-extra-info{
    max-width: 90%;
  }
  .hotel-item{
    padding:10px 15px;
    background:#fff;
    border-bottom: 1px solid #E9E9E9;
  }

  .hotel-popup{
    width: 100%;
  }
  .hotel-popup .mint-button--normal{
    background:url(../../../images/up_icon_grey@3x.png) #fff 80% 20px no-repeat;
    background-size: 12px auto;
  }
 
</style>


