<template>
  <li class="order-item">
    <ul class="order-item-bd list-info">
      <li>
        <label class="tl">宿舍：</label>
        <span>{{hotel}}</span>
      </li>
      <li>
        <label class="tl">房间：</label>
        <span>{{room}}</span>
      </li>
      <li>
        <label class="tl">时间：</label>
        <span>{{time}}</span>
      </li>
    </ul>
    <div class="order-item-ft">
        <span class="orange money">{{ price }}</span>
        <router-link :to="href" class="btn-link">{{btnTxt}}</router-link>
    </div>
  </li>

</template>


<script>
  export default {
    name: 'order-item',

    props: {
      hotel: {
        type: String,
        default: ''
      },
      room: {
        type: String,
        default: ''
      },
      time: {
        type: String,
        default: ''
      },
      price: {
        type: String,
        default:''
      },
      href: {
        type: String,
        default:''
      },
      btnTxt: {
        type: String,
        default:''
      }
    }

  };
</script>

<style lang="css">
  .order-item{
    margin:8px 0;
    padding:15px 15px 0;
    background: #FFFFFF;
    border: 1px solid #E9E9E9;
  }
  .order-item-bd{
    margin-bottom:10px;
    font-size:16px;
  }
  .order-item-bd li label{
    width:50px;
    color:#1E1E1E;
  }
  .order-item-ft{
    display:flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    border-top: 1px dashed #D7D7D7;
  }

  .order-item-ft .money{
    font-size: 17px;
  }

  .order-item-ft .btn-link{
    padding:5px 20px;
    font-size:15px;
    color:#F9621D;
    background: #FFFFFF;
    border: 1px solid #F9621D;
    border-radius: 4px;
  }
</style>



