<template>
  <div class="page-date">

    <mt-header title="入住离店时间">
      <div @click="back" slot="left" >
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>

    <div class="box">
      <calendar :range="calendar.range"  :value="calendar.value" :zero="calendar.zero" :lunar="calendar.lunar" :begin="calendar.begin" :end="calendar.end" @select="calendar.select"></calendar>
    </div>

  </div>
</template>

<script>
import calendar from '../../calendar/calendar.vue'

export default {
  name: 'page-date',

  components: {
    calendar
  },

  data(){
      return {
          count: 0,

          calendar: {
              show:false,
              range:true,
              zero:true,
              value:[], //默认日期
              select:(begin,end)=>{
                this.calendar.value=[begin,end];
                let total = this.dateDiff(begin.join("-"),end.join("-"));
                this.count = total;
                let data = {
                    begin: begin,
                    end: end,
                    count: this.count
                };

                let dataString = JSON.stringify(data);
                localStorage.setItem("booking-date", dataString);
              }
          }
      }
  },

  mounted() {
      if(this.calendar.value.length<2){
        setTimeout(() => {
          this.$toast('请选择入住和离店时间')
        }, 500)
        
      }
  },

  methods:{
    back () {
      if(this.calendar.value.length == 2 && this.count < 1){
        this.$toast('入住时间至少一天以上')
      }else{
        window.history.back();
      }
    },

    //计算天数差的函数，日期格式: yyyy-mm-dd
    dateDiff (DateOne,DateTwo) {
      let OneMonth = DateOne.substring(5,DateOne.lastIndexOf ('-')); 
      let OneDay = DateOne.substring(DateOne.length,DateOne.lastIndexOf ('-')+1); 
      let OneYear = DateOne.substring(0,DateOne.indexOf ('-')); 
   
      let TwoMonth = DateTwo.substring(5,DateTwo.lastIndexOf ('-')); 
      let TwoDay = DateTwo.substring(DateTwo.length,DateTwo.lastIndexOf ('-')+1); 
      let TwoYear = DateTwo.substring(0,DateTwo.indexOf ('-')); 
   
      let cha=((Date.parse(OneMonth+'/'+OneDay+'/'+OneYear)- Date.parse(TwoMonth+'/'+TwoDay+'/'+TwoYear))/86400000);  
      return Math.abs(cha); 
    }
  }
};
</script>

<style lang="css">

</style>


